.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.logo {
  font-size: 24px;
  font-weight: bold;
}

.nav ul {
  display: flex;
  list-style: none;
  padding: 0;
}

.nav li {
  margin-right: 20px;
}

.nav li a {
  text-decoration: none;
  color: #000;
}

.main {
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
}

.search-container {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
}

.search-container input {
  flex: 1;
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.search-container button {
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  border-radius: 4px;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
}

.product-item {
  flex: 1 1 30%;
  margin: 10px;
  padding: 20px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.product-item img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-bottom: 10px;
}

.product-item a {
  text-decoration: none;
  color: #007bff;
  margin-bottom: 10px;
}

.product-item button {
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  border-radius: 4px;
}

.product-item button:hover {
  opacity: 0.8;
}
