body {
  font-family: Arial, sans-serif;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #f8f8f8;
  border-bottom: 1px solid #ddd;
}

.logo {
  font-size: 24px;
  font-weight: bold;
}

.search input {
  padding: 5px;
  font-size: 16px;
}

.nav-links a {
  margin-left: 20px;
  text-decoration: none;
  color: black;
}

.nav-links a:hover {
  text-decoration: underline;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.product-card {
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
  margin: 10px;
  width: 200px;
  text-align: center;
}

.product-card img {
  width: 100%;
  height: auto;
}

.product-info {
  padding: 10px;
}

form {
  display: flex;
  flex-direction: column;
  width: 300px;
  margin: 20px auto;
}

form div {
  margin-bottom: 15px;
}

form label {
  margin-bottom: 5px;
}

