.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.title {
    text-align: left;
    margin-bottom: 20px;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.input-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
    width: 100%;
}

.row {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 20px;
}

.column {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-right: 10px;
}

.column:last-child {
    margin-right: 0;
}

.label {
    margin-bottom: 10px;
    font-weight: bold;
}

.input, .select, .textarea {
    padding: 10px;
    box-sizing: border-box;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 10px;
}

.input {
    width: 100%;
}

.select {
    width: 100%;
}

.textarea {
    width: 100%;
    height: 200px;
}

.image-upload-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
}

.image-upload-label {
    margin-bottom: 10px;
    font-weight: bold;
}

.image-preview {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
}

.image-item {
    margin: 5px;
}

.image-item img {
    width: 100px;
    height: 100px;
    object-fit: cover;
}

.button-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.button {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    color: white;
}

.button:first-child {
    background-color: red;
    margin-right: 10px;
}

.button:last-child {
    background-color: #007bff;
}

.button:hover {
    opacity: 0.8;
}